// pages/recipes/[id].tsx
import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import Link from 'next/link';
import {
  Box,
  Typography,
  List,
  ListItemButton,
  Divider,
} from '@mui/material';

interface Meal {
  idMeal: string;
  strMeal: string;
  strArea: string;
  strCategory: string;
  strMealThumb: string;
  strInstructions: string;
  [key: string]: any;
}

export default function RecipeInfoPage() {
  const router = useRouter();
  const { id } = router.query;
  const [meal, setMeal] = useState<Meal | null>(null);
  const [sidebarList, setSidebarList] = useState<Meal[]>([]);
  const [ingredients, setIngredients] = useState<string[]>([]);

  useEffect(() => {
    if (!id) return;
    fetch(`http://localhost:3000/recipes/${id}`)
      .then((res) => res.json())
      .then((data) => {
        const fetchedMeal: Meal = data.meals[0];
        setMeal(fetchedMeal);
        const ingr: string[] = [];
        for (let i = 1; i <= 20; i++) {
          const name = fetchedMeal[`strIngredient${i}`];
          if (name) {
            ingr.push(name);
          }
        }
        setIngredients(ingr);
        const category = fetchedMeal.strCategory;
        return fetch(`http://localhost:3000/recipes?category=${encodeURIComponent(category)}`);
      })
      .then((res) => res.json())
      .then((data) => {
        const list: Meal[] = data.meals || [];
        setSidebarList(list);
      });
  }, [id]);

  if (!meal) {
    return <Typography align="center">Loading...</Typography>;
  }

  return (
    <Box display="flex" p={2}>
      <Box flex={1} pr={2}>
        <Box position="relative" mb={2}>
          <Box
            component="img"
            src={meal.strMealThumb}
            alt={meal.strMeal}
            width={200}
            height={200}
            sx={{ position: 'absolute', top: 0, left: 0 }}
          />
          <Typography
            variant="h4"
            align="center"
            sx={{ width: '100%', pt: 2 }}
          >
            {meal.strMeal}
          </Typography>
          <Box textAlign="center" mt={1}>
            <Link href={`/recipes?country=${encodeURIComponent(meal.strArea)}`} passHref>
              <Typography
                component="a"
                variant="subtitle1"
                color="primary"
              >
                {meal.strArea}
              </Typography>
            </Link>
          </Box>
        </Box>
        <Box mt={4}>
          <Typography variant="h6" align="center" gutterBottom>
            Instructions
          </Typography>
          <Typography>{meal.strInstructions}</Typography>
        </Box>
        <Box mt={4}>
          <Typography variant="h6" align="center" gutterBottom>
            Ingredients
          </Typography>
          <List>
            {ingredients.map((ing, idx) => (
              <ListItemButton
                key={idx}
                component={Link}
                href={`/recipes?ingredient=${encodeURIComponent(ing)}`}
              >
                <Typography>{ing}</Typography>
              </ListItemButton>
            ))}
          </List>
        </Box>
      </Box>
      <Divider orientation="vertical" flexItem />
      <Box width={300} pl={2}>
        <Typography variant="h6" gutterBottom>
          More in {meal.strCategory}
        </Typography>
        <List>
          {sidebarList.map((item) => (
            <ListItemButton
              key={item.idMeal}
              component={Link}
              href={`/recipes/${item.idMeal}`}
            >
              <Typography>{item.strMeal}</Typography>
            </ListItemButton>
          ))}
        </List>
      </Box>
    </Box>
  );
}
